import React, { lazy, Suspense } from "react";
import { Redirect, Route, Switch } from 'react-router-dom';
import Loading from 'components/shared-components/Loading';

const Apps = ({ match, systemData }) => {
  const ClientsComp = lazy(() => import(`./clients`));
  const InspectionsComp = lazy(() => import(`./inspections`));
  const PaymentsComp = lazy(() => import(`./payments`));
  const PublicTapComp = lazy(() => import(`./publicTap`));
  const SitesComp = lazy(() => import(`./sites`));
  const SitesManagersComp = lazy(() => import(`./sitesManagers`));
  const SysSettingsComp = lazy(() => import(`./syssettings`));
  const UsersComp = lazy(() => import(`./users`));

  const EmployeesComp = lazy(() => import(`./employees`));
  const ExpensesComp = lazy(() => import(`./expenses`));
  const OthersComp = lazy(() => import(`./others`)); 
  const CountriesComp = lazy(() => import(`./countries`));
  const BordersComp = lazy(() => import(`./borders`));
  const DestinationsComp = lazy(() => import(`./destinations`));
  const SuppliersComp = lazy(() => import(`./suppliers`));
  const TrucksComp = lazy(() => import(`./trucks`));
  const MilleageComp = lazy(() => import(`./milleage`));
  const GarageComp = lazy(() => import(`./garage`));
  const TaxComp = lazy(() => import(`./taxes`));
  const FuelComp = lazy(() => import(`./fuels`));
  const OrdersComp = lazy(() => import(`./orders`));
  const CurrenciesComp = lazy(() => import(`./currencies`));
  const TripsComp = lazy(() => import(`./trips`));
  const RoadTallsComp = lazy(() => import(`./roadTalls`));
  const OutsourcingComp = lazy(() => import(`./outsourcing`));
  const TruckRentalComp = lazy(() => import(`./rental`));
  const LoansComp = lazy(() => import(`./loans`));
  const LoanPaymentsComp = lazy(() => import(`./loanPayments`));
  const ReportComp = lazy(() => import(`./report`));
  const TypesComp = lazy(() => import(`./reqTypes`));
  const RequesitionsComp = lazy(() => import(`./requisitions`));
  const ReqTorentComp = lazy(() => import(`./reqTorent`));
  const ReqInsurerComp = lazy(() => import(`./reqToInsurer`));
 
  return ( 
    <Suspense fallback={<Loading cover="content"/>}>
      <Switch>
        <Route path={`${match}/employees`} render={ () => (<EmployeesComp systemData={systemData} match={`${match}/employees`}/>) }/>
        <Route path={`${match}/expenses`} render={ () => (<ExpensesComp systemData={systemData} match={`${match}/expenses`}/>) }/>
        <Route path={`${match}/others`} render={ () => (<OthersComp systemData={systemData} match={`${match}/others`}/>) }/>
        <Route path={`${match}/suppliers`} render={ () => (<SuppliersComp systemData={systemData} match={`${match}/suppliers`}/>) }/>
        <Route path={`${match}/garage`} render={ () => (<GarageComp systemData={systemData} match={`${match}/garage`}/>) }/>
        <Route path={`${match}/countries`} render={ () => (<CountriesComp systemData={systemData} match={`${match}/countries`}/>) }/>
        <Route path={`${match}/borders`} render={ () => (<BordersComp systemData={systemData} match={`${match}/borders`}/>) }/>
        <Route path={`${match}/destinations`} render={ () => (<DestinationsComp systemData={systemData} match={`${match}/destinations`}/>) }/>
        <Route path={`${match}/trucks`} render={ () => (<TrucksComp systemData={systemData} match={`${match}/trucks`}/>) }/>
        <Route path={`${match}/milleage`} render={ () => (<MilleageComp systemData={systemData} match={`${match}/milleage`}/>) }/>
        <Route path={`${match}/taxes`} render={ () => (<TaxComp systemData={systemData} match={`${match}/taxes`}/>) }/>
        <Route path={`${match}/fuels`} render={ () => (<FuelComp systemData={systemData} match={`${match}/fuels`}/>) }/>
        <Route path={`${match}/orders`} render={ () => (<OrdersComp systemData={systemData} match={`${match}/orders`}/>) }/>
        <Route path={`${match}/currencies`} render={ () => (<CurrenciesComp systemData={systemData} match={`${match}/currencies`}/>) }/>
        <Route path={`${match}/trips`} render={ () => (<TripsComp systemData={systemData} match={`${match}/trips`}/>) }/>
        <Route path={`${match}/roadTalls`} render={ () => (<RoadTallsComp systemData={systemData} match={`${match}/roadTalls`}/>) }/>
        <Route path={`${match}/outsourcing`} render={ () => (<OutsourcingComp systemData={systemData} match={`${match}/outsourcing`}/>) }/>
        <Route path={`${match}/truckRental`} render={ () => (<TruckRentalComp systemData={systemData} match={`${match}/truckRental`}/>) }/>
        <Route path={`${match}/loans`} render={ () => (<LoansComp systemData={systemData} match={`${match}/loans`}/>) }/>
        <Route path={`${match}/loanPayments`} render={ () => (<LoanPaymentsComp systemData={systemData} match={`${match}/loanPayments`}/>) }/>
        <Route path={`${match}/types`} render={ () => (<TypesComp systemData={systemData} match={`${match}/types`}/>) }/>
        <Route path={`${match}/requisitions`} render={ () => (<RequesitionsComp systemData={systemData} match={`${match}/requisitions`}/>) }/>
        <Route path={`${match}/torent`} render={ () => (<ReqTorentComp systemData={systemData} match={`${match}/torent`}/>) }/>
        <Route path={`${match}/toinsurer`} render={ () => (<ReqInsurerComp systemData={systemData} match={`${match}/toinsurer`}/>) }/>
        <Route path={`${match}/report`} render={ () => (<ReportComp systemData={systemData} match={`${match}/report`}/>) }/>

        <Route path={`${match}/clients`} render={ () => (<ClientsComp systemData={systemData} match={`${match}/clients`}/>) }/>
        <Route path={`${match}/inspections`} render={ () => (<InspectionsComp systemData={systemData} match={`${match}/inspections`}/>) }/>
        <Route path={`${match}/payments`} render={ () => (<PaymentsComp systemData={systemData} match={`${match}/payments`}/>) }/>
        <Route path={`${match}/publicTap`} render={ () => (<PublicTapComp systemData={systemData} match={`${match}/publicTap`}/>) }/>
        <Route path={`${match}/profile`} component={lazy(() => import(`./profile`))} />
        <Route path={`${match}/sites`} render={ () => (<SitesComp systemData={systemData} match={`${match}/sites`}/>) }/>
        <Route path={`${match}/sitesManagers`} render={ () => (<SitesManagersComp systemData={systemData} match={`${match}/sitesManagers`}/>) }/>
        <Route path={`${match}/systemSettings`} render={ () => (<SysSettingsComp systemData={systemData} match={`${match}/systemSettings`}/>) }/>
        <Route path={`${match}/users`} render={ () => (<UsersComp systemData={systemData} match={`${match}/users`}/>) }/>
        <Redirect from={`${match}`} to={`${match}/employees`} />
      </Switch>
    </Suspense>
  );
}
export default Apps;